* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

a, a:hover {
  text-decoration: none; }

ul {
  list-style: none; }

body {
  font-family: 'Lato';
  font-weight: 400; }

.text-center {
  text-align: center; }

.title {
  text-align: center;
  text-transform: uppercase;
  color: #4c76ff;
  font-weight: 900;
  margin-bottom: 25px;
  font-size: 28px; }

.heart {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 78px; }
  .heart .fa {
    font-size: 20px;
    color: #bfbfbf;
    margin: 0 14px; }
  .heart:before, .heart:after {
    content: "";
    width: 90px;
    height: 2px;
    background: #bfbfbf; }

.pd {
  padding: 100px 0; }

/* #######################    HEADER    ##################### */
.header {
  background: url("../img/header_bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  text-align: center; }
  .header-nav {
    background-color: rgba(16, 22, 54, 0.2); }
    @media (max-width: 767.98px) {
      .header-nav {
        padding: 20px 0; } }
  .header__container {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .header .logo {
    font-weight: 900;
    font-size: 21px;
    color: #fff;
    position: relative;
    z-index: 23; }
    .header .logo span {
      color: #4c76ff; }
  .header-menu {
    max-width: 312px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    @media (max-width: 767.98px) {
      .header-menu {
        position: fixed;
        max-width: 100%;
        width: 100%;
        height: 100%;
        background: #062033;
        left: 0;
        top: 0;
        z-index: 22;
        flex-direction: column;
        justify-content: center; } }
    .header-menu__link {
      color: #fff;
      font-weight: 100; }
      @media (max-width: 767.98px) {
        .header-menu__link {
          font-size: 25px; } }
    .header-menu__item {
      padding: 25px 0; }
      .header-menu__item_active {
        border-top: 2px solid #4c76ff; }
  .header__title {
    color: #fff;
    font-weight: 900;
    font-size: 62px;
    margin: 242px 0 15px; }
    .header__title span {
      color: #4c76ff; }
  .header__subtitle {
    font-weight: 100;
    color: #fff;
    font-size: 48px;
    margin-bottom: 26px; }
    .header__subtitle span {
      color: #4c76ff; }
  .header__text {
    font-weight: 100;
    font-size: 18px;
    color: #fff;
    margin-bottom: 100px;
    display: flex;
    justify-content: center;
    align-items: center; }
    .header__text:before {
      content: "";
      width: 28px;
      height: 2px;
      background: #4c76ff;
      margin-right: 40px; }
    .header__text:after {
      content: "";
      width: 28px;
      height: 2px;
      background: #4c76ff;
      margin-left: 40px; }
  .header-social {
    padding-bottom: 122px; }
    .header-social__item {
      color: #fff;
      opacity: 0.5;
      margin: 0 9px;
      display: inline-block;
      font-size: 18px; }
      .header-social__item:hover {
        opacity: 1; }
  .header__burger {
    width: 40px;
    height: 40px;
    background: none;
    border: 2px solid #fff;
    color: #fff;
    border-radius: 5px;
    font-size: 25px;
    outline: none;
    display: none; }
    @media (max-width: 767.98px) {
      .header__burger {
        display: block; } }
    .header__burger .fa-close {
      display: none; }

/* #######################    HEADER END   ##################### */
/* ########################     section features ###################### */
.features {
  background: #efefef; }
  .features-text {
    margin-left: 14px;
    color: #062033;
    font-family: 'Open Sans';
    font-weight: 400;
    padding-top: 3px; }
    .features-text__title {
      font-weight: 600;
      font-size: 17px;
      margin-bottom: 19px; }
    .features-text__subtitle {
      font-size: 11px;
      line-height: 18px; }

/* ########################     section features END ###################### */
/* ########################     section mobile END ###################### */
.mobile__subtitle {
  max-width: 790px;
  margin: 0 auto 66px;
  text-align: center;
  font-family: 'Open Sans';
  font-weight: 400;
  line-height: 30px; }

.mobile-item {
  display: flex;
  align-items: flex-start;
  color: #282528;
  padding: 14px 8px 14px 14px;
  font-family: 'Open Sans';
  font-weight: 400;
  transition: 0.7s;
  margin: 54px 0;
  cursor: default; }
  .mobile-item:hover {
    background: #fefce9; }
  .mobile-item__text {
    margin-left: 38px; }
  .mobile-item__title {
    font-weight: 700;
    font-size: 13.5px;
    margin-bottom: 19px; }
  .mobile-item__subtitle {
    font-size: 11px;
    line-height: 20px; }

.mobile__iphone {
  width: 100%; }

/* ########################     section mobile END ###################### */
/* ########################     section works END ###################### */
.works.pd {
  padding: 30px 0 0; }

.works__subtitle {
  max-width: 715px;
  margin: 0 auto 73px;
  text-align: center;
  font-weight: 100;
  font-size: 14px;
  color: #062033;
  line-height: 18px; }

.works-gallery__buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 78px; }

.works-gallery__btn {
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
  padding: 10px 18px;
  font-weight: 900;
  font-size: 14px;
  color: #062033;
  margin: 0 11.5px; }
  .works-gallery__btn_active {
    background: #4c76ff;
    color: #fff;
    border-radius: 6px; }

.works-gallery__content {
  display: flex;
  flex-wrap: wrap; }

.works-gallery__item {
  width: 25%;
  position: relative; }
  .works-gallery__item:hover .works-gallery__hover {
    opacity: 1; }

.works-gallery__img {
  width: 100%;
  height: 100%; }

.works-gallery__hover {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(76, 118, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  opacity: 0;
  transition: 0.7s; }

/* ########################     section works END ###################### */
/* ########################     section team ###################### */
.team__subtitle {
  max-width: 800px;
  text-align: center;
  margin: 0 auto 100px;
  font-size: 15px;
  color: #062033;
  line-height: 20px; }

.team-card {
  position: relative; }
  .team-card:hover .team-card__item {
    display: block; }
  .team-card__img {
    width: 100%;
    height: 100%; }
  .team-card__item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(76, 118, 255, 0.9);
    color: #fff;
    text-align: center;
    padding: 20px 25px 19px;
    font-family: 'Open Sans';
    font-weight: 700;
    font-size: 13.2px;
    line-height: 20px;
    display: none; }
  .team-card__title {
    margin-bottom: 14px; }
  .team-card__subtitle {
    font-weight: 400;
    margin-bottom: 50px; }
  .team-card__link {
    display: inline-flex;
    width: 35px;
    height: 35px;
    justify-content: center;
    align-items: center;
    color: #fff;
    background: rgba(0, 0, 0, 0.3);
    font-size: 18px; }

/* ########################     section team END ###################### */
/* ########################     section facts ###################### */
.facts {
  background: url("../img/f_bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  text-align: center;
  color: #fff; }
  .facts__numbers {
    margin: 38px 0 14px;
    font-family: 'Open Sans';
    font-weight: 600;
    font-size: 45px; }
  .facts__text {
    text-transform: uppercase;
    font-family: 'Open Sans';
    font-weight: 400;
    font-size: 14px; }

/* ########################     section facts END ###################### */
/* ########################     section contact ###################### */
.contact__subtitle {
  text-align: center;
  font-size: 18px;
  color: #062033;
  margin-bottom: 100px; }

.contact-address {
  font-family: 'Open Sans';
  font-weight: 600; }
  .contact-address__title {
    font-size: 19px;
    color: #062033;
    line-height: 26.7px;
    margin-bottom: 34px; }
  .contact-address__item {
    font-weight: 400;
    font-size: 11px;
    color: #6a737b;
    line-height: 20px; }

.contact-form {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-family: 'Open Sans';
  font-weight: 400; }
  .contact-form__title {
    width: 100%;
    font-weight: 700;
    color: #062033;
    margin-bottom: 42px; }
  .contact-form__input, .contact-form__textarea {
    width: 49%;
