    border: none;
    border-bottom: 2px solid #e0e0e0;
    margin-bottom: 39px;
    font-size: 11px;
    color: #5b646e; }
    .contact-form__input::placeholder, .contact-form__textarea::placeholder {
      color: #5b646e; }
  .contact-form__textarea {
    width: 100%;
    resize: none;
    padding-bottom: 8px; }
  .contact-form__btn {
