    background: none;
    outline: none;
    cursor: pointer;
    font-size: 13.5px;
    color: #000; }
    .contact-form__btn .fa {
      color: #4c76ff;
      font-size: 17px; }

.contact__link {
  font-size: 30px;
  color: #cdd2d6;
  margin-bottom: 18px;
  margin-left: auto; }

/* ########################     section contact END ###################### */
/* ########################     MAP ###################### */
.map {
  width: 100%;
  height: 200px;
  filter: grayscale(100%); }

/* ########################     MAP END ###################### */
/* ########################     footer  ###################### */
